.favorites {
    &__title {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 40px;
        h1 {
            margin-bottom: 0;
        }
    }
    &-page {
        padding-bottom: 40px;
        .card-img {
            height: 135px;
        }
        .card-full {
            position: relative;
        }
        .card-body__wrap {
            flex-shrink: 1;
        }
        .card-full>.Instock {
            position: absolute;
            top: 40px;
            right: 10px;
            z-index: 1;
            display: none;
        }
        .card-full .card-body {
            justify-content: space-between;
            margin-right: 70px;
            flex-shrink: 1;
        }
        .card-full .card-link {
            margin-right: 0;
            flex-shrink: 1;
        }
        .basket__item-btn {
            margin-left: 30px;
            align-self: center;
        }
    }
}
@media(max-width: 1550px) {
    .favorites {
        &-page {
            .card-full .card-body {
                margin-right: 30px;
            }
            .card-full .card-bottom .count__wrap {
                margin-right: 20px;
            }
        }
    }
}
@media(max-width: 991px) {
    .favorites {
        &__title {
            margin-bottom: 30px;
        }
        &-page {
            .card-full .card-link {
                padding-right: 20px;
            }
            .card-full .card-body {
                margin-right: 0;
            }
            .card-full .card-body .card-action {
                left: 110px;
            }
        }
    }
}
@media(max-width: 767px) {
    .favorites {
        &-page {
            padding-bottom: 20px;
        }
    }
}
@media(max-width: 750px) {
    .favorites {
        &-page {
            .card-Item {
                flex: 0 0 100%;
                max-width: 100%;
            }
        }
    }
}
@media(max-width: 575px) {
    .favorites {
        &__title {
            flex-direction: column;
            align-items: flex-start;
            margin-bottom: 20px;
            h1 {
                margin-bottom: 13px;
            }
            button {
                margin-left: 0;
            }
        }
        &-page {
            .card-img {
                position: relative;
                margin-top: 30px;
            }
            .card-full>.Instock {
                display: block;
            }
            .card-full .card-body {
                margin-right: 0;
            }
            .card-full .card-link {
                padding-right: 0;
            }
            .basket__item-btn {
                top: 8px;
                right: 10px;
            }
            .card-full .card-bottom .count__wrap {
                margin-right: 0;
            }
        }
    }
}
@media (max-width: 400px) {
    .favorites {
        &__title {
            button {
                margin-top: 0;
            }
        }
    }
}